// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
// Element Ui
import Element from 'element-ui'
import locale from 'element-ui/lib/locale/lang/es'
import 'element-ui/lib/theme-chalk/index.css'

// bootstrap
/*import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'*/

// App
import App from './App'

// Vue Router
import router from './router'

// Vuex: for services, shared components, etc
import store from './store/index'

// Our Style
import '../static/style.css'

Vue.config.productionTip = false

Vue.use(/*BootstrapVue,*/ Element, {
  locale
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {
    App
  },
  data: {
        images: ['http://www.desdeelexilio.com/wp-content/uploads/INTERESES.jpg', 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTEr_tTHxVnYaKXy4IQCrc7qsaORidssE2PELtW1Xmb5D-qDBTo', 'https://revistainversionista.files.wordpress.com/2013/06/sistemasinfo1.png', 'http://www.estrategiaynegocios.net/csp/mediapool/sites/dt.common.streams.StreamServer.cls?STREAMOID=Y$BJwqfbMRsrrNG3Cbm39c$daE2N3K4ZzOUsqbU5sYvjnZQOiv5Nr0n6bpn9rGYHWCsjLu883Ygn4B49Lvm9bPe2QeMKQdVeZmXF$9l$4uCZ8QDXhaHEp3rvzXRJFdy0KqPHLoMevcTLo3h8xh70Y6N_U_CryOsw6FTOdKL_jpQ-&CONTENTTYPE=image/jpeg'],
        currentNumber: 0,
        timer: null
    },

    mounted: function () {
        this.startRotation();
    },

    methods: {
        startRotation: function() {
            this.timer = setInterval(this.next, 3000);
        },

        stopRotation: function() {
            clearTimeout(this.timer);
            this.timer = null;
        },

        next: function() {
            this.currentNumber += 1
        }
    }
});



