<style>
.carousel-view {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.carousel {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  
  width: 80%;
  min-height: 25em;
}

.slide {
  flex: 0 0 20em;
  height: 20em;
  margin: 1em;

  display: flex;
  justify-content: center;
  align-items: center;

  border: 0.1em dashed #000;
  border-radius: 50%;

  transition: transform 0.3s ease-in-out;
}

.slide:first-of-type {
  opacity: 0;
}

.slide:last-of-type {
  opacity: 0;
}
</style>

<template>
<div>
<h2>Inicio - <small>Financial App Center</small>
</h2>
<!-- <div class='carousel-view'>
    <transition-group
      class='carousel'
      tag="div">
      <div
      v-for="slide in slides" 
        class='slide'
        :key="slide.id">
      <img :src="slide.href"/>
        </div>
    </transition-group>
    <div class='carousel-controls'>
      <el-button type="primary"  @click="previous">Anterior</el-button>
      <el-button type="primary"  @click="next">Siguiente</el-button>
    </div>
  </div>-->
<center>
<img src="http://conceptodefinicion.de/wp-content/uploads/2017/10/Sistema_Financiero.jpg"/>
</center>
  </div>
</template>


<script>
export default {
  
  name: 'Default',
  data () {
    return {
      slides: [
        {
          title: 'Financial App',
          id: 1,
          href: 'http://www.desdeelexilio.com/wp-content/uploads/INTERESES.jpg'
        },
        {
          title: 'Financial App',
          id: 2,
          href: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTEr_tTHxVnYaKXy4IQCrc7qsaORidssE2PELtW1Xmb5D-qDBTo'
        },
        {
          title: 'Financial App',
          id: 3,
          href: 'https://revistainversionista.files.wordpress.com/2013/06/sistemasinfo1.png'
        },
        {
          title: 'Financial App',
          id: 4,
          href: 'http://www.estrategiaynegocios.net/csp/mediapool/sites/dt.common.streams.StreamServer.cls?STREAMOID=Y$BJwqfbMRsrrNG3Cbm39c$daE2N3K4ZzOUsqbU5sYvjnZQOiv5Nr0n6bpn9rGYHWCsjLu883Ygn4B49Lvm9bPe2QeMKQdVeZmXF$9l$4uCZ8QDXhaHEp3rvzXRJFdy0KqPHLoMevcTLo3h8xh70Y6N_U_CryOsw6FTOdKL_jpQ-&CONTENTTYPE=image/jpeg'
        }
      ]
    }
  },

  methods: {
    next () {
      const first = this.slides.shift()
      this.slides = this.slides.concat(first)
    },
    previous () {
      const last = this.slides.pop()
      this.slides = [last].concat(this.slides)
    }
  }
}
</script>





